import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
var _self = this,
	page = 1;
// 定义全局参数,控制数据加载
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			keywords: '',
			isSearch: false,
			showResult: false,
			helpKeyword: [],
			historyKeywords: [],
			maxNumKeyowrdsInStorage:15,
			categoryFilter: false,
			currentSortType: 'default',
			currentSortOrder: '',
			// 定义页面路径 
			goodsPath: '/pages/product/product',
			categoryPath: '/pages/category/categoryDetail',
			brandPath: '/pages/test/test',
			filterCategory: [],
			defaultKeyword: {},
			hotKeyword: [],
			page: 1,
			pageLength: 20,
			categoryId: 0,
			resultList: [],
			categoryFolderName: "category",
			goodsFolderName: "goods",
			brandFolderName: "brand",
			brandtypeName: 'BRAND',
			cagegoryTypeName: 'CATEGORY',
			goodstypeName: 'GOODS',
			// 定义上拉加载更多变量
			loadingText: '加载中...',
			loadingType: 'more', //定义加载方式 more---contentdown  loading---contentrefresh noMore---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
		};
	},
	onLoad(options) {
		// 缓存中定义的存储搜索历史的容器name：_HISTORY_KEYWORDS_LIST
		var storageData = this.$cache.get('_HISTORY_KEYWORDS_LIST');
		if(storageData == null){
			this.$cache.set('_HISTORY_KEYWORDS_LIST',[],3600*24*30);
			storageData = this.$cache.get('_HISTORY_KEYWORDS_LIST');
		}
		var tempData = storageData;
		if(tempData.length > 0){
			tempData.reverse();
		}
		// 将缓存中的数据倒序排列存储
		this.historyKeywords = tempData;
	},
	onReachBottom: function() {
		this.searchResult();
	},
	methods: {
		//事件处理函数
		closeSearch: function() {
			wx.navigateBack()
		},
		clearKeywords() {
			this.keywords = '';
		},
		clearHistory(){
			this.$cache.delete('_HISTORY_KEYWORDS_LIST');
			var _self = this;
			_self.historyKeywords = [];
		},
		listenKeywordsInput() {
			this.showResult = false;
		},
		confirmKeywords(event) {
			let _self = this;
			_self.showResult = true;
			_self.resultList = [];
			_self.loadingType = 'more';
			_self.page = 1;
			_self.searchResult();
			if(_self.keywords != ''){
				_self.handlSearchKeywords(_self.keywords);
			}
		},
		handlSearchKeywords(keywords){
			var _self = this;
			var storageData = this.$cache.get('_HISTORY_KEYWORDS_LIST');
			if(storageData.length > 0 && storageData.indexOf(keywords) > 0){
				var index = storageData.indexOf(keywords);
				storageData.splice(index,1);
			}
			storageData.push(keywords);
			if(storageData.length > _self.maxNumKeyowrdsInStorage){
				storageData.shift();
			}
			this.$cache.set('_HISTORY_KEYWORDS_LIST',storageData,3600*24*30);
			var tempData = storageData;
			if(tempData.length > 0){
				tempData.reverse();
			}
			_self.historyKeywords = tempData;
		},
		onKeywordTap(item){
			this.keywords = item;
			this.confirmKeywords();
		},
		searchResult() {
			let _self = this;
			if (_self.loadingType != 'more') {
				return ;
			}
			_self.loadingType = 'loading';
			uni.showNavigationBarLoading(); //显示加载动画
			_self.$minApi.uniapp_post(_self.$base_api.search, {
				'mallId': _self.$base_api.MALL_ID,
				'keywords': _self.keywords,
				page: _self.page,
				pageLength: _self.pageLength
			}).then(res => {
				if (res.success) {
					var tempData = res.data;
					if (res.data.length == 0) { //没有数据
						_self.loadingType = 'noMore';
						uni.hideNavigationBarLoading(); //关闭加载动画
						return;
					}
					_self.page++; // 每触底一次 page + 1
					tempData.forEach(item => {
						let folderName = '';
						if (item.type == _self.brandtypeName) {
							folderName = _self.brandFolderName;
						} else if (item.type == _self.cagegoryTypeName) {
							folderName = _self.categoryFolderName;
						} else {
							folderName = _self.goodsFolderName;
						}
						item.url = _self.$base_api.fileReview + '?mallId=' + _self.$base_api.MALL_ID + '&folderName=' +
							folderName + '&fileName=' + item.url;
					});
					_self.loadingType = tempData.length < _self.pageLength ? 'noMore' : 'more';
					console.log("_self.loadingType",_self.loadingType);
					_self.resultList = _self.resultList.concat(tempData); //将数据拼接在一起
					uni.hideNavigationBarLoading(); //关闭加载动画
					console.log("查询结果 resultList:", _self.resultList);
				} else {
					console.log("查询失败")
				}
				_self.showResult = true;
			});
		},
		navToPage(item) {
			var path;
			if (item.type == this.brandtypeName) {
				path = this.brandPath;
				item.brandId = item.id;
			} else if (item.type == this.cagegoryTypeName) {
				path = this.categoryPath;
				item.categoryId = item.id;
			} else {
				path = this.goodsPath;
				item.goodsId = item.id;
			}
			uni.navigateTo({
				url: path + '?item=' + encodeURIComponent(JSON.stringify(item))
			})
		},

	},


};
